.tapeBody {
  cursor: pointer;
  margin-top: 10px;
  margin-bottom: 10px;
  border-radius: 10px;
  box-sizing: border-box;
  max-width: 900px;
  width: 100%;
  overflow: hidden;
  text-decoration: none;
  composes: colorLightBorder from './../../../../styles/color.module.css';
}

.overflow {
  box-sizing: border-box;
  padding: 20px 24px;
  display: flex;
  composes: colorLightBackground from './../../../../styles/color.module.css';
}

.circleButton {
  width: 50px;
  min-width: 50px;
  height: 50px;
  border-radius: 100%;
  background: #1a1a1a;
  display: flex;
  justify-content: center;
  align-items: center;
}

.icon {
  filter: invert(0);
  width: 22px;
  height: 22px;
}

.container {
  margin-left: 15px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  composes: colorLightTitle from './../../../../styles/color.module.css';
}

.title {
  width: 100%;
  max-width: 900px;
  font-family: Roboto;
  font-style: normal;
  font-weight: 600;
  font-size: 17px;
  letter-spacing: 0.1px;
  margin-bottom: 0px;
  margin-top: 0px;
  min-height: 25px;
  height: 25px;
  max-height: 25px;
  padding-top: 6px;
  box-sizing: border-box;
  user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  composes: colorLightTitle from './../../../../styles/color.module.css';
}

.timeContainer {
  user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  width: 100%;
  max-width: 900px;
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 17px;
  line-height: 25px;
  letter-spacing: 0.1px;
  margin-top: 4px;
  composes: colorLightDescription from './../../../../styles/color.module.css';
}

@media (max-width: 640px) {
  .tapeBody {
    border-radius: 8px;
  }

  .overflow {
    padding: 15px 12px;
  }

  .title {
    min-height: 25px;
    height: 25px;
    box-sizing: border-box;
    letter-spacing: 0px;
    font-size: 16px;
    line-height: 22px;
  }

  .timeContainer {
    margin-top: 3px;
    letter-spacing: 0px;
    font-size: 16px;
    line-height: 22px;
  }
}
