.tapeBody {
  font-size: 0px;
  cursor: pointer;
  margin-top: 20px;
  margin-bottom: 20px;
  border-radius: 10px;
  box-sizing: border-box;
  max-width: 900px;
  width: 100%;
  overflow: hidden;
  text-decoration: none;
  display: flex;
  composes: colorDarkBackground from './../../../../styles/color.module.css';
  composes: colorDarkBorder from './../../../../styles/color.module.css';
}

.overflow {
  width: 40%;
  border-radius: 10px;
  box-sizing: border-box;
  padding: 30px 24px;
  composes: colorDarkBorder from './../../../../styles/color.module.css';
  composes: colorDarkBackground from './../../../../styles/color.module.css';
}

@media (max-width: 938px) {
  .tapeBody {
    display: block;
  }

  .overflow {
    border: none;
    border-radius: 0px;
    box-shadow: none;
    width: 100%;
  }
}

@media (max-width: 640px) {
  .tapeBody {
    border-radius: 8px;
  }

  .overflow {
    padding: 15px 19px;
  }
}
